body {
  background-color: #1d3e7a;
}

.App {
  margin: 12px;
}

@font-face {
  font-family: "OpenSans";
  src: url("/fonts/OpenSans-Regular.ttf") format("truetype");
}

.App p {
  text-align: justify;
  font-family: "Open Sans", sans-serif;
  color: #e9f4ff;
}

.App h1 {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  color: #9af0ff;
}

.App h2 {
  text-align: justify;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  color: #9af0ff;
}

.App h3 {
  text-align: justify;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  color: #9af0ff;
}

a {
  color: #dd4a4a;
}

a:visited {
  color: gray;
}

.carousel-inner {
  padding: 30px;
  margin: auto;
  width: 80%;
  background-color: rgb(0, 110, 255);
  text-align: center;
}

.game-embed
{
  display: flex;
  justify-content: center;
  align-items: center;
}

